<template>
  <section class="login">
    <header class="login-header">
      <h1 class="brand">
        <router-link to="/" tabindex="-1">
          {{ header.name }}
        </router-link>
      </h1>
      <el-alert 
        v-if="error" 
        :title="error.title" 
        :description="error.message" 
        type="warning" 
        show-icon />
    </header>
    <el-form 
      :model="model" 
      :rules="rules" 
      class="login-form" 
      auto-complete="off" 
      ref="login-form" 
      label-position="top">
      <h2 class="heading">Sign-in</h2>
      <el-form-item label="Username" prop="username">
        <el-input 
          v-model="model.username" 
          type="text" 
          @keyup.enter.native="submit('login-form')" />
      </el-form-item>
      <el-form-item label="Password" prop="password">
        <el-input 
          v-model="model.password"
          type="password" 
          @keyup.enter.native="submit('login-form')" />
      </el-form-item>
      <el-button 
        type="primary" 
        :loading="loading" 
        @click="submit('login-form')">
        {{ loading ? 'Loading...' : 'Login' }}
      </el-button>
    </el-form>
    <!-- <footer class="login-footer">
      ‚Üê Back to <a href="/">{{ header.name }}</a>
    </footer> -->
  </section>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'app-login',

  data () {
    // form model
    // TODO: remove default values
    const model = {
      username: null,
      password: null
    }

    // form validate rules
    const rules = {
      username: [
        { required: true, message: 'Username is required' },
        { min: 2, max: 16, message: 'Username must be between 2 and 16 characters' }
      ],
      password: [
        { required: true, message: 'Password is required' },
        { min: 4, max: 16, message: 'Password must be between 4 and 16 characters' }
      ]
    }

    return { model: model, rules: rules, error: null, loading: false }
  },

  computed: {
    ...mapGetters(['header'])
  },

  methods: {
    submit (ref) {
      // form validate
      this.$refs[ref].validate(valid => {
        if (!valid) return false

        // validated
        this.error = null
        this.loading = true

        // create token from remote
        this.$store.dispatch('createToken', this.model)
          .then(token => {
            this.$router.replace({ path: this.$route.query.redirect || '/' })
          })
          .catch(err => {
            this.error = { title: 'Error occurred', message: 'Abnormal, please try again later!' }
            switch (err.response && err.response.status) {
              case 401:
                this.error.message = 'Incorrect username or password!'
                break
              case 500:
                this.error.message = 'Internal server error, please try again later!'
                break
            }
          })
          .finally(() => this.loading = false)
      })
    }
  }
}
</script>

<style lang="scss">
  @import '../../assets/styles/variables';

  .login {
    flex: 1;
    width: 95%;
    max-width: 22rem;
    margin: 0 auto;
    font-size: .875rem;

    &-header {
      margin-bottom: 1rem;
      
      .brand {
        margin: 4.5rem 0 3.5rem;
        text-align: center;
        letter-spacing: .125rem;

        a {
          margin: 0;
          color: $brand-color;
          font: 300 3rem sans-serif;

          &:hover {
            color: $brand-hover-color;
            text-shadow: 0 0 1rem $brand-hover-color;
          }
        }
      }
    }

    &-form {
      margin-bottom: 2.5rem;
      padding: 1.875rem 1.25rem;
      background: $login-form-background;
      color: $login-form-color;

      .heading {
        margin: 0 0 1rem;
        font-weight: 400;
        font-size: 1.5rem;
      }

      .el-button {
        margin-top: .5rem;
        width: 100%;
      }
    }

    &-footer {
      margin-bottom: 1rem;
      padding: .625rem;
      border: .0625rem solid $brand-color;
      font-size: .75rem;
      text-align: center;

      a {
        color: $brand-color;
      }
    }
  }
</style>



